<template>
    <div class="home">
        <div class="site-content animate">
            <main class="site-main" >
                <section-title v-if="!hideSlogan">Recommend</section-title>
                <section-title v-else-if="category">{{category}}</section-title>
                <section-title v-else-if="searchWords">Search results:"{{searchWords}}" related articles</section-title>
                <template v-for="item in postList">
                    <post :post="item" :key="item.id"></post>
                </template>
            </main>

            <!--more-->
            <div class="more" v-show="hasNextPage>currPage">
                <div class="more-btn" @click="loadMore">More</div>
            </div>
        </div>
    </div>
</template>

<script>
  
  
    import sectionTitle from '@/components/section-title'
    import Post from '@/components/post'
    import SmallIco from '@/components/small-ico'
    import Quote from '@/components/quote'
    import {fetchList} from '../api'

    export default {
        name: 'Home',
        props: ['cate', 'words'],
        data() {
            return {
                postList: [],
                currPage: 1,
                hasNextPage: 1
            }
        },
        components: {
            sectionTitle,
            Post,
            SmallIco,
            Quote
        },
        computed: {
            searchWords() {
               
                return this.$route.params.words
            },
            category() {
                return this.$route.meta.title
            },
            hideSlogan() {
                return this.category || this.searchWords
            },
            notice() {
                return this.$store.getters.notice
            }
        },
        watch: {
           "$route.meta.title": "fetchList"
        },
        methods: {
            fetchList() {
                fetchList({"type_id":this.$route.meta.id,"words":this.$route.params.words}).then(res => {
              
                    this.postList = res.data.data || []
                    this.currPage = res.data.current_page
                    this.hasNextPage = res.data.last_page
                }).catch(err => {
                    console.log(err)
                })
            },
            loadMore() {
                fetchList({page:this.currPage+1,"type_id":this.$route.meta.id,"words":this.$route.params.words}).then(res => {
                    this.postList = this.postList.concat(res.data.data || [])
                    this.currPage = res.data.current_page
                    this.hasNextPage = res.data.last_page
                })
            }
        },
        created() {

            this.fetchList();
        }
    }
</script>
<style scoped lang="less">

    .site-content {
        .notify {
            margin: 60px 0;
            border-radius: 3px;
            & > div {
                padding: 20px;
            }
        }


        .search-result {
            padding: 15px 20px;
            text-align: center;
            font-size: 20px;
            font-weight: 400;
            border: 1px dashed #ddd;
            color: #828282;
        }
    }

    .top-feature {
        width: 100%;
        height: auto;
        margin-top: 30px;

        .feature-content {
            margin-top: 10px;
            display: flex;
            justify-content: space-between;
            position: relative;

            .feature-item {
                width: 32.9%;
            }
        }
    }

    .site-main {
        padding-top: 120px;

        &.search {
            padding-top: 0;
        }
    }

    .more{
        margin: 50px 0;
        .more-btn{
            width: 100px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            color: #ADADAD;
            border: 1px solid #ADADAD;
            border-radius: 20px;
            margin: 0 auto;
            cursor: pointer;
            &:hover{
                color: #8fd0cc;
                border: 1px dashed #8fd0cc;
            }
        }
    }

    /******/
    @media (max-width: 800px) {
        .top-feature {
            display: none;
        }

        .site-main {
            padding-top: 40px;
        }

        .site-content {
            .notify {
                margin: 30px 0 0 0;
            }

            .search-result {
                margin-bottom: 20px;
                font-size: 16px;
            }
        }
    }

    /******/
</style>
